import styled from "styled-components";
import { InnerWrap, Text, Title2 } from "../../assets/styles/component";

import icon_assigment from "../../assets/images/icon_assignment.svg";
import icon_creditcard from "../../assets/images/icon_credit-card.svg";
import icon_electricity from "../../assets/images/icon_electricity.svg";
import icon_tag from "../../assets/images/icon_tag.svg";
import icon_volunteer from "../../assets/images/icon_volunteer.svg";
import icon_crown from "../../assets/images/icon_crown.svg";
import Future_1 from "../../assets/images/Future_1.webp";
import Future_2 from "../../assets/images/Future_2.webp";
import { useEffect, useRef, useState } from "react";

const SFuture = styled.section`
  transform: translateY(-100px);
  overflow: hidden;
  background: radial-gradient(
      87.41% 29.55% at 49.09% 103.28%,
      #06a5d8 20.47%,
      rgba(0, 51, 233, 0.637795) 59.04%,
      rgba(41, 1, 157, 0) 100%
    ),
    radial-gradient(
      68.14% 62.26% at 42.1% -14.94%,
      #ecbbc3 0%,
      rgba(200, 62, 198, 0.637795) 57.81%,
      rgba(160, 9, 230, 0) 100%
    ),
    #29009c;
  border-radius: 106px;
  padding: 150px 0 240px 0;

  & > div > p {
    text-align: center;
    margin: 0 auto;
  }

  @media (max-width: 1300px) {
    padding: 70px 0 140px;
    border-radius: 60px;
  }
`;

const Title = styled(Title2)`
  margin-bottom: 30px;
`;

const Frame = styled.div`
  margin: 0 ato;
  overflow: hidden;
  max-width: 999px;
  width: 100%;
  height: fit-content;
  min-height: 38vw;
  outline: 20px solid #ffffff22;
  border-radius: 70px;
  background: radial-gradient(
    240.7% 80.8% at 107.77% 65.48%,
    #a927ca 0%,
    #d72f75 41.67%,
    #e79079 76.04%,
    #e3592f 98.44%
  );
  margin: 100px auto;

  iframe {
    height: 38vw;
    margin-bottom: -4px;
  }

  @media (max-width: 1300px) {
    max-width: 55vw;
    border-radius: 22px;
    outline: 9px solid #ffffff22;
    min-height: 30vw;

    iframe {
      height: 30vw;
    }
  }
`;

const Links = styled.div`
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  margin: 0 auto;
  margin-top: 100px;
  max-width: 1386px;
  width: 100%;

  @media (max-width: 900px) {
    width: 110%;
    margin-left: -5%;
    transform: scale(0.8);
  }

  @media (max-width: 700px) {
    width: 121%;
    margin-left: -10.5%;
    transform: scale(0.8);
  }
`;

const Link = styled.div`
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 3px 20px;

  @media (max-width: 1300px) {
    p {
      font-size: 12px;
    }
  }

  ${({ color }) => {
    switch (color) {
      case "orange":
        return `
        background: #e3592f;
        box-shadow: inset 0px 0px 38px rgba(255, 255, 255, 0.29);
        border-radius: 51px;
        transform: rotate(-4.66deg); `;

      case "pink":
        return `
        background: #D72F75;
        box-shadow: inset 0px 0px 38px rgba(255, 255, 255, 0.29);
        border-radius: 51px;
        transform: translate(-3px, 45px) rotate(5.5deg); `;

      case "whiteblue":
        return `
        background: #0079E6;
        box-shadow: inset 0px 0px 38px rgba(255, 255, 255, 0.29);
        border-radius: 51px;
        transform: translate(-10px, -20px) rotate(-6.03deg); `;

      case "blue":
        return `
        background: #1010F2;
        box-shadow: inset 0px 0px 38px rgba(255, 255, 255, 0.29);
        border-radius: 51px;
        transform: translate(-10px, 30px) rotate(4.63deg); `;

      case "yellow":
        return `
        background: #EBA050;
        box-shadow: inset 0px 0px 38px rgba(255, 255, 255, 0.29);
        border-radius: 51px;
        transform: translate(-45%, 50px) rotate(-1.76deg); `;

      case "violet":
        return `
        background: #9C00E8;
        box-shadow: inset 0px 0px 38px rgba(255, 255, 255, 0.29);
        border-radius: 51px;
        transform: translate(10%, 60px) rotate(-6.78deg); `;
    }
  }}

  @media (max-width: 1300px) {
    padding: 3px 29px 3px 13px;
    gap: 10px;
  }

  @media (max-width: 1000px) {
    padding: 3px 13px;
    gap: 2px;
  }
`;

const Icon = styled.img`
  margin-right: 16px;

  @media (max-width: 1300px) {
    margin-right: 8px;
    transform: scale(0.8);
  }

  @media (max-width: 1000px) {
    transform: scale(0.8);
    max-width: 30px;
  }
`;

const Future = ({ language }) => {
  return (
    <SFuture>
      <InnerWrap>
        <Title>
          Как будет выглядеть <br /> ваша кампания
        </Title>

        <Frame className={"active"} active={true}>
          <iframe
            width="100%"
            height="100%"
            src="https://www.youtube.com/embed/WIfDD26wfYQ?autoplay=1"
            title="SocialStream"
            frameBorder="0"
            allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture; web-share"
            allowFullScreen
            autoplay
          ></iframe>
        </Frame>

        <Title>А что еще?</Title>

        <Text>
          Вместо обычных ссылок вы сможете интегрировать <br /> в неограниченное
          количество стримов уникальный промо-формат
        </Text>

        <Links>
          <Link color={"orange"}>
            <Icon src={icon_electricity} alt="Idea" loading="lazy" />
            <Text size="24">User-generated контент</Text>
          </Link>

          <Link color={"pink"}>
            <Icon src={icon_creditcard} alt="Card" loading="lazy" />
            <Text size="24">Поддержка подписок</Text>
          </Link>

          <Link color={"whiteblue"}>
            <Icon src={icon_tag} alt="Tag" loading="lazy" />
            <Text size="24">White label</Text>
          </Link>

          <Link color={"blue"}>
            <Icon src={icon_assigment} alt="Document" loading="lazy" />
            <Text size="24">Full service</Text>
          </Link>

          <Link color={"yellow"}>
            <Icon src={icon_volunteer} alt="Loyalty" loading="lazy" />
            <Text size="24">Лояльность аудитории</Text>
          </Link>

          <Link color={"violet"}>
            <Icon src={icon_crown} alt="Crown" loading="lazy" />
            <Text size="24">Тонкий таргетинг</Text>
          </Link>
        </Links>
      </InnerWrap>
    </SFuture>
  );
};

export default Future;
